import React, {Component} from 'react';
import preloader from './images/preloader.gif';
import './App.css';
import Video from './Video.js';
import Logo from './Logo.js'
import Interface from './Interface.js'


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false
    };
  }

  checkPageLoading = () => {
    window.onload = () => {
      setTimeout(()=>{
        this.setState({
          isLoaded: true
        })
      }, 2000)
    }
  }

  componentDidMount() {

  }

  render () {
    let preloaderScreenClass = `preloader-screen`;
    if (this.state.isLoaded === true) {
      preloaderScreenClass = `preloader-screen loaded`
    }
    return (
        <div className="App">
          <div className={preloaderScreenClass}>
            <img src={preloader} alt="site preloader" className="preloader"/>
          </div>
          {this.checkPageLoading()}
          <div className="main-page">
            <Video />
          </div>
          <Logo isPageLoaded={this.state.isLoaded} />
          <Interface isPageLoaded={this.state.isLoaded} />
        </div>
    );
  }
}

export default App;
